import React from 'react';
import '../../assets/css/confirmdeletemodal.css';

export default function ConfirmDeleteModal({ onConfirm, onCancel }) {
  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <p>Bn c chc mun xo cng vic ny khng?</p>
        <div className="modal-actions">
          <button onClick={onCancel} className="modal-btn cancel">Hu</button>
          <button onClick={onConfirm} className="modal-btn confirm">Xo</button>
        </div>
      </div>
    </div>
  );
}
